
import React, { Component } from 'react';
import Swiper from 'swiper';
import { Pagination, Autoplay } from 'swiper';

class Header extends Component {
  componentDidMount(){
    Swiper.use([ Pagination, Autoplay  ]);
    var swiper = new Swiper(".mySwiper", {
      pagination: {
        el: ".swiper-pagination",
      },
      autoplay: {
        delay: 5000,
      },
      pagination: {
        el: ".swiper-pagination",
        clickable: true
      },
    });
    
    var header=document.getElementById("header");
    var top=header.firstElementChild;
    var [logo, tab]=top.children;
  
    var gnb=document.getElementById("gnb");
    var dim=document.getElementsByClassName("dim")[0];
  
    var body=document.body;
    var wrapper=body.firstElementChild;
  
    var depth1Ul=gnb.firstElementChild;
    var depth1Li=depth1Ul.children;
    
    var page=[]; 
    for(var i=0; i<wrapper.children.length; i++){
      if(wrapper.children[i].id.indexOf("page") !==-1){
        page.push(wrapper.children[i]);
      }
    }
    
    console.log(page[0]);
    var mainN;
    var t=0;
  
  
    tab.addEventListener("click", function(e){
      e.preventDefault();
      gnb.classList.add("active");
      dim.classList.add("active");
      body.classList.add("fixed");
    });
    dim.addEventListener("click", function(e){
      e.preventDefault();
      gnb.classList.remove("active");
      dim.classList.remove("active");
      body.classList.remove("fixed");	
  
      setTimeout(() => {
        for(var i=0; i<depth1Li.length; i++){
          if(depth1Li[i].classList.contains("active")){
            depth1Li[i].classList.remove("active");
            depth1Li[i].lastElementChild.style.height="0px";
          }
        }
      }, 300);
    });
  
    var depth2Height=[];
  
    for(var i=0; i<depth1Li.length; i++){
      depth1Li[i].idx=i;
      depth2Height.push(depth1Li[i].lastElementChild.offsetHeight);
      depth1Li[i].lastElementChild.style.height="0px";
  
      depth1Li[i].addEventListener("click",function(e){
        e.preventDefault();
        var n=e.currentTarget.idx;	
        for(var j=0; j<depth1Li.length; j++){
          if(n == j){
            if(e.currentTarget.classList.contains("active") == false){
              depth1Li[j].classList.add("active");
              depth1Li[j].lastElementChild.style.height=depth2Height[j]+"px";
            }
            else{
              depth1Li[j].classList.remove("active");
              depth1Li[j].lastElementChild.style.height="0px";
            }
          }
          else{
            depth1Li[j].classList.remove("active");
            depth1Li[j].lastElementChild.style.height="0px";
          }
        }
      });
    }
  }
	render() {
		return(
      <React.Fragment>
        <header id="header">
          <div className="top">
            <div className="logo"><h1><a href="/">FRIGE FESTIVAL</a></h1></div>
            <div className="tab">
              <a href="/">
                <span></span>
              </a>
            </div>
          </div>
          <div className="swiper mySwiper">
            <div className="swiper-wrapper">
              <div className="swiper-slide">
                <div className="text">
                  <div className="main_text">부산<br />불꽃놀이<br />페스티벌</div>
                  <div className="sub_text"> 사람과 예술이 만나<br />생생한 문화의 꽃이 피어나는 곳</div>
                </div>
              </div>
              <div className="swiper-slide">
                <div className="text">
                  <div className="main_text">청춘<br />Hip Hop<br />페스티벌</div>
                  <div className="sub_text"> 사람과 예술이 만나<br />생생한 문화의 꽃이 피어나는 곳</div>
                </div>
              </div>
              <div className="swiper-slide">
                <div className="text">
                  <div className="main_text">안양<br />Music<br /> 콘서트</div>
                  <div className="sub_text"> 사람과 예술이 만나<br />생생한 문화의 꽃이 피어나는 곳</div>
                </div>
              </div>
              <div className="swiper-slide">
                <div className="text">
                  <div className="main_text">서울<br />Music<br />페스티벌</div>
                  <div className="sub_text"> 사람과 예술이 만나<br />생생한 문화의 꽃이 피어나는 곳</div>
                </div>
              </div>
            </div>
            <div className="swiper-pagination"></div>
          </div>
        </header>
        <div className="menu">
          <nav id="gnb">
            <ul>
              <li>
                <a href="/">About SFF</a>
                <ul>
                  <li><a href="/">About</a></li>
                  <li><a href="/">서울프린지페스티벌2021</a></li>
                  <li><a href="/">페스티벌공간</a></li>
                  <li><a href="/">만든는 사람들</a></li>
                  <li><a href="/">아카이브</a></li>
                </ul>
              </li>
              <li>
                <a href="/">페스티벌 가이드</a>
                <ul>
                  <li><a href="/">현장가이드</a></li>
                  <li><a href="/">자주하는 질문</a></li>
                  <li><a href="/">오시는 길</a></li>
                </ul>
              </li>
              <li>
                <a href="#ticket">티켓구매</a>
                <ul>
                  <li><a href="/">티켓구매 바로가기</a></li>
                </ul>
              </li>
              <li>
                <a href="#comunity">커뮤니티</a>
                <ul>
                  <li><a href="/">공지사항</a></li>
                  <li><a href="/">뉴스레터</a></li>
                  <li><a href="/">현장스케치</a></li>
                  <li><a href="/">페스티벌 사진</a></li>
                  <li><a href="/">언론 속 프린지</a></li>
                </ul>
              </li>
              <li>
                <a href="#donate">후원하기</a>	
                <ul>
                  <li><a href="/">프린지 후원하기</a></li>
                  <li><a href="/">함께한 사람들</a></li>
                </ul>
              </li>
            </ul>
          </nav>
        </div>
        <div className="dim"></div>
      </React.Fragment>
	  );
	}
}

export default Header;

   